import Alpine from "alpinejs";
import intersect from "@alpinejs/intersect";

console.log("[DEBUG] Starting main.js execution");

// Initialize Alpine.js
try {
  console.log("[DEBUG] Initializing Alpine.js");
  window.Alpine = Alpine;
  Alpine.plugin(intersect);
  Alpine.start();
  console.log("[DEBUG] Alpine.js initialized successfully");
} catch (error) {
  console.error("[DEBUG] Error initializing Alpine.js:", error);
}

// Simple test animation using Tailwind classes
document.addEventListener("DOMContentLoaded", () => {
  const testElement = document.querySelector(".test-animation");
  if (testElement) {
    testElement.classList.add(
      "animate-spin",
      "duration-2000",
      "ease-in-out",
      "infinite"
    );
  }
});

// Page Transitions
const pageTransition = () => {
  const main = document.querySelector("main");
  if (main) {
    main.classList.add("transition-opacity", "duration-500", "ease-in-out");
  }

  window.addEventListener("beforeunload", () => {
    if (main) {
      main.classList.add("opacity-0");
    }
  });
};

// Section Animations
const initSectionAnimations = () => {
  // Hero section animation
  const hero = document.querySelector(".hero-content");
  if (hero) {
    hero.classList.add(
      "opacity-0",
      "translate-y-12",
      "transition-all",
      "duration-1000",
      "ease-out"
    );
    setTimeout(() => {
      hero.classList.remove("opacity-0", "translate-y-12");
    }, 100);
  }

  // Services cards animation
  document.querySelectorAll(".service-card").forEach((card, i) => {
    card.classList.add(
      "opacity-0",
      "translate-y-8",
      "transition-all",
      "duration-600",
      "ease-out"
    );
    setTimeout(() => {
      card.classList.remove("opacity-0", "translate-y-8");
    }, 200 + i * 200);
  });

  // Testimonials animation
  document.querySelectorAll(".testimonial").forEach((testimonial, i) => {
    testimonial.classList.add(
      "opacity-0",
      "-translate-x-8",
      "transition-all",
      "duration-800",
      "ease-out"
    );
    setTimeout(() => {
      testimonial.classList.remove("opacity-0", "-translate-x-8");
    }, 200 + i * 200);
  });
};

// Initialize all animations
document.addEventListener("DOMContentLoaded", () => {
  pageTransition();
  initSectionAnimations();
});
